@import "styles";

.contact-wrapper {
  min-height: 15vh;
  svg {
    width: 50%;
    height: auto;
  }
  a {
    display: inherit;
  }
}
.contact-row {
  display: flex;
  min-width: 0;
  gap: 5%;
  padding-bottom: 2em;
}

@media screen and (min-width: $tablet) {
  // center contact row
  .contact-row {
    justify-content: center;
  }
}
